tabset.tab-wizard {
  .nav-tabs {
    border-bottom: $border-width solid $border-color;
    pointer-events: none;

    .nav-link {
      border-color: transparent;
      &.active {
        background-color: transparent;
        border-bottom: 3px solid acerta-color('blue-dark');
        color: acerta-color('blue-dark');
      }
    }

    .nav-item {
      a {
        color: acerta-color('utility-green');
      }
    }

    .nav-item.active ~ .nav-item {
      a {
        color: acerta-color('grey-dark');
      }
    }
  }
}
