.table {
  border: 1px solid acerta-color('grey-light');

  thead {
    background-color: acerta-color('grey-lighter');

    th {
      border: 1px solid acerta-color('grey-light');
      padding: 8px;
    }
  }
}

.table-borderless {
  border: 0;

  thead {
    th {
      border: 0;
    }
  }
}

.table-striped {
  thead {
    background-color: transparent;
  }
}

.table-sm {
  > thead th, td, tr {
    height: 30px;
    line-height: 28px;
    padding: 0 7px;
  }
}
