@import "../config/colors";
@import "../config/variables";

/*
** INSTRUCTIONS **

Use as follows:
* Add package "ag-grid-community" to your project dependencies
* In your project's styles root:
  * Import this file to customize and extend the ag-grid theme
  * Import ag-grid's css file and it's bootstrap theme sass file

----------------------------------------------------------
@import "~@acerta/ui/assets/css/addons/ag-grid-theme-vars";
@import "~ag-grid-community/dist/styles/ag-grid.css";
@import "~ag-grid-community/src/styles/ag-theme-bootstrap/sass/ag-theme-bootstrap";
----------------------------------------------------------
*/


/*
** VARIABLES **
*/
// Colors
$border-color: acerta-color('grey-light');
$background-color: acerta-color('white');
$foreground-color: $body-color;
$header-background-color: acerta-color('grey-lighter');

$header-foreground-color: $foreground-color !default;
$icon-color: acerta-color('grey-light');
$alt-icon-color: $background-color !default;
$accent-color: transparent !default;
$range-selection-background-color: $icon-color !default;

// Sizing
$grid-size: 4px !default;
$icon-size: 12px !default;
$header-height: 28px;
$row-height: 30px;
// $row-height: 28px;
$cell-horizontal-padding: 8px; // .5rem
// $virtual-item-height: $grid-size * 5 !default;
$virtual-item-height: $grid-size * 10;
$header-icon-size: 14px !default;

// Fonts
$font-family: $font-family-base;
$font-size: 12.8px;
$secondary-font-size: $font-size;
$secondary-font-weight: 600;


$font-weight: 400 !default;
$secondary-font-family: $font-family !default;

// Tooltip
$tooltip-background-color: #535353 !default;
$tooltip-foreground-color: $foreground-color !default;
$tooltip-border-radius: 2px !default;
$tooltip-padding: 5px !default;
$tooltip-border-width: 1px !default;
$tooltip-border-style: solid !default;
$tooltip-border-color: #ebebeb !default;

// Misc
$card-shadow: none !default;
$card-radius: 0 !default;
$row-border-width: 1px;
$transition-speed: 120ms !default;
$cell-horizontal-border: 1px solid transparent !default;

$toolpanel-indent-size: $grid-size + $icon-size !default;
$row-group-indent-size: $grid-size * 3 + $icon-size !default;

$header-cell-moving-background-color: rgb(190, 190, 190) !default;
$input-disabled-background-color: #ebebeb !default;
$input-border-color: #000000 !default;
$scroll-spacer-border: 1px solid if($border-color, $border-color, lightgrey) !default;

$dialog-background-color: $background-color !default;
$dialog-border-size: 1px !default;
$dialog-border-style: solid !default;
$dialog-border-color: $border-color !default;

$dialog-title-background-color: $header-background-color !default;
$dialog-title-foreground-color: $header-foreground-color !default;
$dialog-title-height: $header-height !default;
$dialog-title-font-family: $secondary-font-family !default;
$dialog-title-font-size: $secondary-font-size !default;
$dialog-title-font-weight: $secondary-font-weight !default;
$dialog-title-padding: 5px 10px !default;
$dialog-title-icon-size: max(20px, $icon-size) !default;

$range-selection-chart-background-color: adjust-hue($range-selection-background-color, 25%) !default;
$range-selection-chart-category-background-color: adjust-hue($range-selection-background-color, -80%) !default;

// Icons
$icon-font-family: "agGridClassic" !default;
$icons-data: "" !default;

$ag-icon-aggregation: "\e900" !default;
$ag-icon-arrows: "\e901" !default;
$ag-icon-asc: "\e902" !default;
$ag-icon-cancel: "\e903" !default;
$ag-icon-chart: "\e904" !default;
$ag-icon-checkbox-checked: "\e905" !default;
$ag-icon-checkbox-indeterminate: "\e906" !default;
$ag-icon-checkbox-unchecked: "\e907" !default;
$ag-icon-column: "\e908" !default;
$ag-icon-columns: "\e909" !default;
$ag-icon-contracted: "\e90a" !default;
$ag-icon-copy: "\e90b" !default;
$ag-icon-cross: "\e90c" !default;
$ag-icon-cut: "\e90d" !default;
$ag-icon-data: "\e90e" !default;
$ag-icon-desc: "\e90f" !default;
$ag-icon-expanded: "\e910" !default;
$ag-icon-eye-slash: "\e911" !default;
$ag-icon-eye: "\e912" !default;
$ag-icon-filter: "\e913" !default;
$ag-icon-first: "\e914" !default;
$ag-icon-grip: "\e915" !default;
$ag-icon-group: "\e916" !default;
$ag-icon-indeterminate: "\e917" !default;
$ag-icon-last: "\e918" !default;
$ag-icon-left: "\e919" !default;
$ag-icon-loading: "\e91a" !default;
$ag-icon-maximize: "\e91b" !default;
$ag-icon-menu: "\e91c" !default;
$ag-icon-minimize: "\e91d" !default;
$ag-icon-minus: "\e91e" !default;
$ag-icon-next: "\e91f" !default;
$ag-icon-none: "\e920" !default;
$ag-icon-not-allowed: "\e921" !default;
$ag-icon-paste: "\e922" !default;
$ag-icon-pin: "\e923" !default;
$ag-icon-pivot: "\e924" !default;
$ag-icon-plus: "\e925" !default;
$ag-icon-previous: "\e926" !default;
$ag-icon-radio-button-off: "\e927" !default;
$ag-icon-radio-button-on: "\e928" !default;
$ag-icon-right: "\e929" !default;
$ag-icon-save: "\e92a" !default;
$ag-icon-small-down: "\e92b" !default;
$ag-icon-small-left: "\e92c" !default;
$ag-icon-small-right: "\e92d" !default;
$ag-icon-small-up: "\e92e" !default;
$ag-icon-tick: "\e92f" !default;
$ag-icon-tree-closed: "\e930" !default;
$ag-icon-tree-indeterminate: "\e931" !default;
$ag-icon-tree-open: "\e932" !default;

$foreground-opacity: 1 !default;
$secondary-foreground-color-opacity: 1 !default;
$disabled-foreground-color-opacity: 0.5 !default;

// Colors
$icon-color: #333 !default;
$alt-icon-color: #fff !default;
$foreground-color: rgba(black, $foreground-opacity) !default;
$secondary-foreground-color: rgba(#333, $secondary-foreground-color-opacity) !default;
$disabled-foreground-color: rgba(black, $disabled-foreground-color-opacity) !default;
$menu-option-active-color: #bde2e5 !default;

$card-background-color: #f6f6f6 !default;
$border-color: transparent !default;
$primary-color: black !default;
$accent-color: black !default;

$background-color: white !default;
$odd-row-background-color: acerta-color('white');

$editor-background-color: #f6f6f6 !default;

$header-foreground-color: $secondary-foreground-color !default;
$header-background-color: transparent !default;
$header-cell-hover-background-color: $header-background-color !default;
$header-background-image: none !default;
$panel-background-color: #f6f6f6 !default;

$tool-panel-background-color: #f6f6f6 !default;

$chip-background-color: #ecf0f1 !default;

$range-selection-background-color: rgba(120, 120, 120, 0.4) !default;
$range-selection-highlight-color: rgba(255, 255, 255, 0.4) !default;

$hover-color: inherit !default;

$selected-color: $menu-option-active-color !default;

$cell-data-changed-color: white !default;

$focused-cell-border-color: darkgrey !default;

$tab-background-color: #e6e6e6;

$cell-highlight-border: 2px solid darkgreen !default;
$cell-horizontal-border: none !default;

$ag-range-selected-color-1: rgba(120, 120, 120, 0.4) !default;
$ag-range-selected-color-2: rgba(80, 80, 80, 0.4) !default;
$ag-range-selected-color-3: rgba(40, 40, 40, 0.4) !default;
$ag-range-selected-color-4: rgba(0, 0, 0, 0.4) !default;

$row-floating-background-color: #f0f0f0 !default;
$row-stub-background-color: #f0f0f0 !default;

// delta changes
$value-change-delta-up-color: darkgreen !default;
$value-change-delta-down-color: darkred !default;
$value-change-value-highlight-background-color: #cec !default;


/*
** EXTENSIONS **
*/
.ag-sort-order {
  order: 1;
  font-size: $font-size-small;
  color: $icon-color;
}
