@import "~bootstrap/scss/functions";

.text-heading {
  font-family: $headings-font-family;
  font-weight: $headings-font-weight;
  line-height: $headings-line-height;
}

.text-h1 { @extend .text-heading; @include font-size($h1-font-size); }
.text-h2 { @extend .text-heading; @include font-size($h2-font-size); }
.text-h3 { @extend .text-heading; @include font-size($h3-font-size); }
.text-h4 { @extend .text-heading; @include font-size($h4-font-size); }
.text-h5 { @extend .text-heading; @include font-size($h5-font-size); }
.text-h6 { @extend .text-heading; @include font-size($h6-font-size); }
