.container-fluid {
  footer {
    margin-left: -15px;
    margin-right: -15px;
  }
}

%aui-footer {
  padding: 1.5rem 0;
}

.aui-footer-light {
  @extend %aui-footer;
  background-color: acerta-color('grey-lighter');
}
.aui-footer-dark {
  @extend %aui-footer;
  background-color: acerta-color('blue-dark');
}
