.form-control-with-icon {
  padding-right: 30px;
}

.form-control-with-icon + i {
  position: absolute;
  right: 0;
  padding: calc(#{$input-padding-y} + #{$input-height-border});
  line-height: $input-line-height * 1em;
}

.form-control-with-spinner {
  padding-right: 2rem;
}

.form-control-with-spinner + .form-control-spinner {
  position: absolute;
  right: 0;
  padding: calc(#{$input-padding-y} + #{$input-height-border});
  z-index: 5;

  .aui-spinner-dots {
    margin-top: 17px;
    margin-left: -10px;

    div {
      height: 6px;
      width: 3px;
      transform-origin: 50% -8px;
    }
  }
}

.form-control-sm.form-control-with-spinner + .form-control-spinner {
  .aui-spinner-dots {
    margin-top: 11px;
    margin-left: -7px;

    div {
      height: 5px;
      width: 3px;
      transform-origin: 50% -6px;
    }
  }
}
