.aui-menu {
  @extend .list-group;

  .list-group-item {
    border-color: acerta-color('grey-light');
    //border-right: none;
    border-left: 5px solid transparent;

    &.active {
      border-left-color: acerta-color('blue-light');
      background-color: acerta-color('grey-lightest');
      color: acerta-color('grey-darker');
      font-weight: bold;
    }
    &[routerLink] {
      cursor: pointer;
    }
  }
}
