@import "./../mixins/bar-shadow";

.aui-tabs {
  display: flex;
  flex: 1 1 auto;
  flex-wrap: wrap;
  margin-left: auto;
  margin-right: auto;
}

.aui-tab {
  display: flex;
  flex: 1 1 auto;
  position: relative;
  @include rem('font-size', 14px);
  font-weight: bold;
  color: acerta-color("grey-dark");
  cursor: pointer;
  min-height: 3rem;
  border-left: 1px solid acerta-color("grey-lighter");
  border-right: 1px solid acerta-color("grey-lighter");
  margin-right: -1px;

  &:hover, &.active {
    color: acerta-color('blue-light');

    &:before {
      bottom: 0;
      width: 100%;
      background-color: acerta-color('blue-light');
    }
  }

  &:before {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 50%;
    height: 5px;
    width: 0;
    background-color: transparent;
    transform: translate3d(-50%, 0, 0);
    transition: width .3s ease-in-out, background-color .3s ease-in-out;
  }
}

.aui-tab-content {
  display: flex;
  flex: 1 1 auto;
  padding-left: 1rem;
  padding-right: 1rem;
  justify-content: center;
  align-items: center;

  .dropdown-item {
    font-weight: $font-weight-bold;
  }
}
