.aui-dropdown {
	@extend .dropdown;
	.dropdown-menu {
    padding: 0;
	}

  .dropdown-item {
    color: acerta-color("grey-dark");
    padding-bottom: 1rem;
    padding-top: 1rem;

    &.active {
      background-color: acerta-color('grey-lightest');
      border-left: 3px solid acerta-color('blue-light');
      font-weight: bold;
    }
    &:hover, &:active {
      background-color: acerta-color('grey-lighter');
    }
  }

  .dropdown-divider {
    margin: 0;
  }
}
